import React from "react";
import "./App.css";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import Icon from "@mui/material/Icon";
import ListItemText from "@mui/material/ListItemText";
import CircularProgress from "@mui/material/CircularProgress";
import ReactJson from "react-json-view";
import { Alert, Divider, ListItem } from "@mui/material";
import Axios from "axios";

const BASEURL = "https://pdf-to-json-v1.herokuapp.com";

function FilePreview({ item, removeHandle, pdfJson, currentStatus }) {
  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

  return (
    <div
      id="filepreview"
      style={{
        display: "grid",
        gridTemplateColumns: "5fr 0fr",
        gridAutoFlow: "column",
        gridTemplateRows: "1fr",
      }}
    >
      <ListItem
        className="nameBtn"
        onClick={handleClick}
        style={{
          flex: 1,
          justifyItems: "center",
          justifyContent: "space-between",
        }}
      >
        <div
          style={{
            flex: 2,
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
          }}
        >
          <ListItemIcon>
            <Icon>picture_as_pdf</Icon>
          </ListItemIcon>
          <ListItemText
            className="filename"
            primary={item.name.slice(0, 30) + "..."}
          />
        </div>
        <div
          style={{
            flex: 1,
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "flex-end",
          }}
        ></div>
      </ListItem>
      <ListItemButton
        onClick={removeHandle}
        sx={{
          flex: 0,
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <ListItemIcon
          style={{ alignItems: "center", justifyContent: "center" }}
        >
          <Icon>delete</Icon>
        </ListItemIcon>
      </ListItemButton>
    </div>
  );
}

function App() {
  const [FilePDF, setFilePDF] = React.useState(null);
  const [PDFJson, setPDFJson] = React.useState(null);
  const [currentStatus, setCurrentStatus] = React.useState(null);
  const [errorMessage, setErrorMessage] = React.useState("");
  const status = ["Start", "Proccessing", "Finished", "Failed"];

  const successCallBack = React.useCallback((data) => {
    setCurrentStatus(2);
    setPDFJson(data);
    console.log(data);
    setErrorMessage("");
  }, []);
  const selectFile = React.useCallback((data) => {
    setErrorMessage("");
    setCurrentStatus(0);
    setPDFJson(null);
    setFilePDF(data);
  }, []);
  const failedCallBack = React.useCallback((data) => {
    setCurrentStatus(3);
    setPDFJson({});
    setErrorMessage(data);
  }, []);

  const uploadFile = () => {
    const formData = new FormData();
    formData.append("pdf", FilePDF);
    setCurrentStatus(1);
    Axios.post(BASEURL + "/getjson", formData, {
      headers: { "Content-Type": "multipart/form-data" },
    })
      .then((data) => {
        if (data.data.status !== 200) {
          failedCallBack(data.data.message);
          return;
        }
        successCallBack(data.data.data);
      })
      .catch((err) => {
        failedCallBack(err);
        console.log("Error: ", err);
      });
  };

  return (
    <Container component="main" style={{ width: "100%" }}>
      <Box
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        {errorMessage && (
          <Alert severity="error">{errorMessage.toString()}</Alert>
        )}
        <Box
          component="form"
          className="btnBox"
          noValidate
          sx={{
            mt: 1,
            width: "100%",

            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <Button
            className="btns"
            type="button"
            fullWidth
            variant="outlined"
            sx={{
              mt: 1,
              mb: 1,
              mr: 1,
              width: "220px",
            }}
          >
            <input
              onChange={({ target: { files } }) => {
                selectFile(files.item(0));
              }}
              accept="application/pdf"
              type="file"
              style={{ opacity: 0, position: "absolute", width: "100%" }}
            />
            Upload PDFs
          </Button>
        </Box>
        <Box
          component="div"
          sx={{
            mt: 1,
            width: "100%",

            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <Divider />

          {FilePDF && (
            <FilePreview removeHandle={() => setFilePDF(null)} item={FilePDF} />
          )}

          <Divider />
        </Box>
        <div
          style={{
            width: "100%",
            minHeight: 200,
            maxWidth: "100%",
            backgroundColor: "rgb(240,240,240)",

            overflowX: "scroll",
            paddingTop: 10,

            borderColor: "#555",
          }}
        >
          {PDFJson && (
            <ReactJson
              enableClipboard={(copy) => {
                console.log(copy);
                return true;
              }}
              src={PDFJson}
              collapsed={false}
            />
          )}
        </div>
        <Button
          className="btns"
          disabled={FilePDF === undefined || FilePDF === null}
          onClick={uploadFile}
          fullWidth
          variant="contained"
          sx={{
            mt: 1,
            mb: 1,
            width: "320px",
            backgroundColor:
              currentStatus === 0
                ? "yellowgreen"
                : currentStatus === 2
                ? "green"
                : currentStatus === 3
                ? "red"
                : "#49f",
          }}
        >
          {currentStatus === null ? "Convert to JSON" : status[currentStatus]}

          {currentStatus === 1 ? (
            <>
              <span style={{ width: 10 }} />
              <CircularProgress
                size={25}
                value={100}
                color="inherit"
                style={{ color: "#fff" }}
              />
            </>
          ) : null}
        </Button>
      </Box>
    </Container>
  );
}

export default App;
